.problems-section {
  position: relative;

  padding-top: 50px;
  background-image: linear-gradient(to bottom, var(--black-color) 84%, var(--white-color) 16%);

  @media screen and (min-width: 768px) {
    padding-top: 60px;
    background-image: linear-gradient(to bottom, var(--black-color) 72%, var(--white-color) 28%);
  }

  @media screen and (min-width: 1394px) {
    padding-top: 100px;

    background-image: linear-gradient(to bottom, var(--black-color) 77%, var(--white-color) 23%);
    overflow-x: hidden;
  }

  &__title--mobile {
    font-family: 'Montserrat', sans-serif;
    font-weight: 800;
    font-size: 20px;
    line-height: 136%;
    letter-spacing: 0.03em;
    color: var(--white-color);

    margin: 0;
    margin-bottom: 27px;

    @media screen and (min-width: 768px) {
      display: none;
    }
  }

  &__title--color {
    color: var(--second-color);
  }

  &__title {
    @media screen and (max-width: 767px) {
      display: none;
    }

    @media screen and (min-width: 768px) {
      font-weight: 800;
      font-size: 26px;
      line-height: 130%;
      letter-spacing: 0.03em;
      color: var(--white-color);

      margin: 0;
      margin-bottom: 25px;
      margin-right: 160px;
    }

    @media screen and (min-width: 1394px) {
      font-weight: 800;
      font-size: 46px;
      line-height: 130%;
      letter-spacing: 0.03em;
      color: var(--white-color);

      margin: 0;
      padding-bottom: 412px;
    }
  }
}

.problems-section__list {
  list-style: none;
  margin: 0;
}

.problems-section {
  &__list-container {
    display: flex;
    background: var(--white-color);
    box-shadow: var(--section-shadow);

    //position-absolute
    @media screen and (min-width: 1394px) {
      position: absolute;
      width: 1314px;
      bottom: 0;
      z-index: 2;
    }
  }

  &__list {
    padding-top: 10px;
    padding-bottom: 20px;
    padding-left: 30px;
    padding-right: 14px;

    counter-reset: section;

    color: var(--black-color);
    font-size: 14px;
    font-weight: 700;
    line-height: 171%;
    letter-spacing: 0.08em;

    @media screen and (min-width: 768px) {
      padding-top: 30px;
      padding-left: 96px;
      padding-right: 96px;
      padding-bottom: 26px;

      display: flex;
      flex-wrap: wrap;
      margin-left: calc(-1 * 30px);
      margin-top: calc(-1 * 30px);

      line-height: 143%;
    }

    @media screen and (min-width: 1394px) {
      padding-top: 70px;
      padding-left: 114px;
      padding-right: 112px;
      padding-bottom: 62px;

      font-size: 20px;
      line-height: 140%;
    }
  }

  // item
  &__item {
    padding-top: 10px;

    @media screen and (min-width: 768px) {
      padding-top: 0px;

      flex-basis: calc(100% / 2 - 30px);
      margin-left: 30px;
      margin-top: 30px;
    }
    @media screen and (min-width: 1394px) {
      flex-basis: calc(100% / 3 - 30px);

      margin-left: 30px;
      margin-top: 30px;
    }
  }

  &__item::before {
    color: var(--second-color);
    counter-increment: section;
    content: '— 0' counter(section);

    @media screen and (min-width: 768px) {
      font-size: 17px;
      line-height: 120%;
      letter-spacing: 0.08em;
    }

    @media screen and (min-width: 1394px) {
      font-size: 20px;
      line-height: 164.9%;
      letter-spacing: 0.08em;
    }
  }

  &__text {
    margin-top: 5px;
    margin-bottom: 0px;

    @media screen and (min-width: 1394px) {
      margin-top: 10px;
    }
  }
}

.problem-section__icon-fire {
  display: none;

  @media screen and (min-width: 1394px) {
    display: block;
    position: absolute;

    left: 1130px;
    bottom: 158px;
    fill: var(--secondary-text-color);
  }

  @media screen and (min-width: 1680px) {
    left: 1273px;
    bottom: 158px;
  }
}
